import React from 'react';
import {
  Form,
  Select,
  InputNumber,
  Switch,
  Radio,
  Slider,
  Button,
  Upload,
  Icon,
  Rate,
  Tooltip,
  Input,
  Table,
  Card,
  Divider,
  DatePicker,
  Row,
  Col,
  Modal,
  message,
  Popconfirm,
  Alert,
  BackTop,
  Descriptions
} from 'antd';
import { ColumnProps, TableRowSelection, TableProps } from 'antd/es/table';
import { SorterResult } from 'antd/es/table';
import store from '../../models/index';
import { FormComponentProps } from 'antd/es/form';
import {
  StandardTableColumnProps,
  TableListParams,
  TableListData,
  TableListPagination,
  TableListItem
} from '../Table';
import { inject, observer } from 'mobx-react';
const FormItem = Form.Item;
const Option = Select.Option;
interface Props {
  onClose: any;
  data: any;
}
interface State {
  columns: StandardTableColumnProps[];
  loading: boolean;
  pagination: TableListPagination;
  commentMaps: any;
  itemTypeMaps: any;
  subClass: any;
}
@inject('orders')
@observer
class SearchOrder extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      subClass: {
        1: '',
        2: '',
        3: ''
      },
      itemTypeMaps: {
        1: "",
        3: "",
        4: "",
        5: "",
      },
      commentMaps: {
        0: '',
        1: '',
        2: '',
        3: ''
      },
      loading: true,
      pagination: {
        total: 20,
        pageSize: 20,
        current: 1
      },
      columns: [
        {
          title: '',
          dataIndex: 'content'
        },
        {
          title: '',
          dataIndex: 'nickName'
        },
        {
          title: '',
          dataIndex: 'itemType',
          render: (text: number) => <span>{this.state.itemTypeMaps[text]}</span>
        },
        {
          title: '',
          dataIndex: 'itemTitle'
        },
        {
          title: '',
          dataIndex: 'state',
          render: (text: number, record: any) => (
            <span>{this.state.commentMaps[text]}</span>
          )
        }
      ]
    };
  }
  componentDidMount() {
    store.orders.getSearchOrder({
      data: {
        runningNumber: this.props.data.runningNumber
      },
      callback: res => {
        if (res.code === 0) {
          this.setState({
            loading: false
          });
        }
      }
    });
  }

  render() {
    const info = store.orders.searchOrder;
    return (
      <Card title="" bordered={false} loading={this.state.loading}>
        <Descriptions title="">
          <Descriptions.Item label="" span={2}>
            {info.runningNumber}
          </Descriptions.Item>
          <Descriptions.Item label="" span={2}>
            {info.name}
          </Descriptions.Item>
          {info.shoppingOrderDetails.map((item: any, index: number) => (
            <Descriptions.Item label="" key={item.id} span={3}>
              <span style={{ width: '100px', display: 'inline-block' }}>
                {item.nickName + '(' + this.state.subClass[item.subClass] + ')'}
              </span>
              <span style={{ marginLeft: '100px' }}>{'' + item.price}</span>
            </Descriptions.Item>
          ))}
          {info.shoppingParticipants.map((item: any, index: number) => (
            <Descriptions.Item key={item.id} label="" span={3}>
              <span style={{ width: '100px', display: 'inline-block' }}>
                {item.name}
              </span>
              <span style={{ marginLeft: '100px' }}>
                {':' + item.phone}
              </span>
            </Descriptions.Item>
          ))}

          <Descriptions.Item label="" span={3}>
            <span style={{ width: '100px', display: 'inline-block' }}>
              {info.userContacts.name}
            </span>
            <span style={{ marginLeft: '100px' }}>
              {':' + info.userContacts.phone}
            </span>
          </Descriptions.Item>
        </Descriptions>
      </Card>
    );
  }
}
export default SearchOrder;
