import React from 'react';
import {
  Form,
  Select,
  InputNumber,
  Switch,
  Radio,
  Slider,
  Button,
  Upload,
  Icon,
  Rate,
  Tooltip,
  Input,
  Table,
  Card,
  Divider,
  DatePicker,
  Row,
  Col,
  Modal,
  message,
  Popconfirm,
  Alert,
  BackTop,
  Descriptions
} from 'antd';
import { ColumnProps, TableRowSelection, TableProps } from 'antd/es/table';
import { SorterResult } from 'antd/es/table';
import store from '../../models/index';
import { FormComponentProps } from 'antd/es/form';
import {
  StandardTableColumnProps,
  TableListParams,
  TableListData,
  TableListPagination,
  TableListItem
} from '../Table';
import { inject, observer } from 'mobx-react';
const FormItem = Form.Item;
const Option = Select.Option;
interface Props {
  onClose: any;
  data: any;
}
interface State {
  loading: boolean;
  commentMaps: any;
  itemTypeMaps: any;
  subClass: any;
}
@inject('orders')
@observer
class AfterSales extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      subClass: {
        1: '',
        2: '',
        3: ''
      },
      itemTypeMaps: {
        1: "",
        3: "",
        4: "",
        5: "",
      },
      commentMaps: {
        0: '',
        1: '',
        2: '',
        3: ''
      },
      loading: true
    };
  }
  componentDidMount() {
    store.orders.getAfterSales({
      data: {
        runningNumber: this.props.data.runningNumber
      },
      callback: res => {
        if (res.code === 0) {
          this.setState({
            loading: false
          });
        }
      }
    });
  }

  render() {
    const info = store.orders.afterSales;
    return (
      <Card title="" bordered={false} loading={this.state.loading}>
        <Descriptions title="">
          <Descriptions.Item label="" span={3}>
            {info.runningNumber}
          </Descriptions.Item>
          <Descriptions.Item label="" span={3}>
            {info.reason}
          </Descriptions.Item>
          <Descriptions.Item label="" span={3}>
            {info.phone}
          </Descriptions.Item>
          <Descriptions.Item label="" span={3}>
            {info.createTime}
          </Descriptions.Item>
        </Descriptions>
      </Card>
    );
  }
}
export default AfterSales;
